import Home from "./components/Home.vue"

const routes = [
  {
    path: '/',
    name: "Home",
    components: Home
  }
]

const router = new VueRouter({
  mode: 'history',
  routes // short for `routes: routes`
})

new Vue({
  render: h => h(App),
  router,
}).$mount('#app')
